.reviews-section .container {
  padding: 50px 20px;
}

.section-title {
  margin-bottom: 20px;

  font-weight: 800;
  font-size: 20px;
  line-height: 1.3;
  text-align: center;
  letter-spacing: 0.03em;

  color: $primary-dark-color;
}

.slider {
  background: #ffffff;
  box-shadow: -5px 8px 26px rgba(0, 0, 0, 0.04);
}

.slider-item {
  padding-top: 20px;
  padding-bottom: 30px;
}

.slider-image {
  margin-bottom: 20px;
}

.slider-image-item {
  margin: 0 auto;
  margin-bottom: 20px;
  width: 90px;
}

.slider-social-link {
  display: block;
  width: 30px;
  height: 30px;
  margin: 0 auto;
}

.slider-social-icon {
  width: 30px;
  height: 30px;
  fill: #436eab;
}

.review-name {
  margin-bottom: 10px;

  font-weight: 800;
  font-size: 14px;
  line-height: 1.214;

  text-align: center;
  letter-spacing: 0.08em;

  color: #ff6b09;
}

.review-institution {
  padding: 0 18px;
  margin-bottom: 10px;

  font-weight: 500;
  font-size: 12px;
  line-height: 1.25;
  text-align: center;
  letter-spacing: 0.08em;

  color: #b9b9b9;
}

.review-text {
  padding: 0 18px;

  font-weight: 500;
  font-size: 12px;
  line-height: 1.666;

  text-align: center;

  color: #3d3d3d;
}

.review-link {
  color: #ff6b09;
}

@media screen and (min-width: 768px) {
  .reviews-section .container {
    padding: 50px 40px;
  }

  .section-title {
    margin-bottom: 30px;

    font-size: 26px;
    line-height: 1.307;
  }

  .slider-item {
    display: flex;
    padding-top: 40px;
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 40px;
  }

  .slider-image {
    margin-bottom: 0;
    margin-right: 46px;
    min-width: 150px;
  }

  .slider-image-item {
    margin: 0;
    margin-bottom: 20px;
    width: 150px;
  }

  .slider-social-link {
    display: block;
    width: 30px;
    height: 30px;
    margin: 0 auto;
  }

  .slider-social-icon {
    width: 30px;
    height: 30px;
    fill: #436eab;
  }

  .review-name {
    margin-bottom: 20px;

    font-size: 20px;
    line-height: 1.2;

    text-align: start;
  }

  .review-institution {
    padding-left: 0;
    margin-bottom: 20px;

    font-size: 12px;
    line-height: 1.25;
    text-align: start;
  }

  .review-text {
    padding: 0;

    text-align: start;
  }
}

@media screen and (min-width: 1680px) {
  .reviews-section .container {
    padding: 130px 112px;
  }

  .section-title {
    margin-bottom: 30px;

    font-size: 46px;
    line-height: 1.304;
  }

  .slider-item {
    display: flex;
    padding-top: 50px;
    padding-left: 82px;
    padding-right: 112px;
    padding-bottom: 40px;
  }

  .slider-image {
    margin-right: 40px;
  }

  .slider-image-item {
    margin: 0;
    margin-bottom: 20px;
    width: 150px;
  }

  .slider-social-link {
    display: block;
    width: 30px;
    height: 30px;
    margin: 0 auto;
  }

  .slider-social-icon {
    width: 30px;
    height: 30px;
    fill: #436eab;
  }

  .review-name {
    margin-bottom: 10px;

    font-size: 30px;
    line-height: 1.233;
  }

  .review-institution {
    font-size: 14px;
    line-height: 1.214;
  }

  .review-text {
    padding: 0;

    font-size: 18px;
    line-height: 1.555;

    text-align: start;
  }
}
