.container {
  padding: 0 15px;
  margin: 0 auto;
  @media screen and (max-width: 767px) {
    min-width: 320px;
    max-width: 480px;
  }
  @media screen and (min-width: 768px) {
    padding: 0 40px;
    width: 768px;
  }
  @media screen and (min-width: 1680px) {
    padding: 0 183px;
    width: 1314px;
    margin: 0 auto;
  }
}
